* {
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
  background-color: #c5e4e7;
  font-family: "Space Mono", monospace;
  font-size: 62.5%;
}
html.font-size {
  font-size: 75%;
}

body {
  margin: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

input:active, input:focus {
  outline-width: 0;
}

a {
  text-decoration: none;
}

p {
  margin: 0px;
  padding: 0px;
}

.input {
  display: block;
}
.input .input-title {
  font-size: 1.2rem;
  font-weight: 400;
  padding-bottom: 0.75rem;
}
.input .input-container {
  position: relative;
}
.input .input-container input {
  width: 100%;
  border: 2px solid transparent;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #f4fafa;
  text-align: right;
  font-size: 2.4rem;
  color: #00494d;
  font-weight: 600;
  caret-color: transparent;
}
.input .input-container input::placeholder {
  color: #c5e4e7;
}
.input .input-container input:focus {
  border: 2px solid #26c0ab;
}
.input .input-container input::-webkit-outer-spin-button,
.input .input-container input::-webkit-inner-spin-button,
.input .input-container input[type=number] {
  -moz-appearance: textfield;
  -webkit-appearance: none;
}
.input .input-container span.left-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(75%, -50%);
}

#container {
  width: 90vw;
  max-width: 800px;
  display: flex;
  background-color: white;
  border-radius: 25px;
}
#container > .box {
  padding: 3rem 2.5rem;
  margin: 2rem;
}
#container > .box--1 {
  flex-basis: 55%;
  margin-right: 0.5rem;
}
#container > .box--2 {
  flex-basis: 45%;
  margin-left: 0.5rem;
  background-color: #00494d;
  border-radius: 25px;
}